@import url(https://fonts.googleapis.com/css?family=B612|Charmonman);
body {
  background-image: url("https://github.com/March-Hdz-Carba/gdl002-burger-queen/blob/desarrollo/imagen/ladrillos.jpg?raw=true");
  margin: 0; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center flex-end;
  background: #000000;
  margin-top: 2%;
  width: 100%;
  height: 80px;
}

.button-position{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  width: 100%;
}





.tittle{
    color: #FBC507;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    font-family: 'B612', sans-serif;
    font-size: 48px;
}

.tittle-p{
    font-family: 'Charmonman', cursive;
    font-size: 64px;
}
.buttonFoodPosition {
